.Header {
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: row;
  background: linear-gradient(to bottom, #003d3e, #004f50);
  height: 50px;
  justify-content: space-between;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 5px;
  z-index: 1000;
  @media screen and (max-width: 700px) {
    padding-right: 0px;
  }

  .logo {
    height: 45px;
    width: 45px;
  }
  .menu {
    display: flex;
    flex-direction: column;
  }
  .iconMenu {
    display: none;
    @media screen and (max-width: 700px) {
      display: flex;
      justify-content: end;
      margin-right: 25px;
      cursor: pointer;
      .iconsMenu {
        position: relative;
        color: white;
        margin-top: 7px;
      }
    }
  }
  .navBar {
    @media screen and (max-width: 700px) {
      animation: fadein 0.8s;
      margin-bottom: 0px;
      margin-top: 0px;
      z-index: 2;
    }
    ul {
      @media screen and (max-width: 700px) {
        background: #004f50;
        padding: 10px;
        margin-top: 0px;
        margin-bottom: 0px;
        overflow: hidden;
      }
    }
    li {
      @media screen and (max-width: 700px) {
        display: flex;
        flex-direction: column;
      }
    }

    .navLink {
      font-family: "Tajawal", sans-serif;
      font-size: 24px;
      margin-left: 30px;
      position: relative;
      color: white;
      text-decoration: none;

      @media screen and (max-width: 700px) {
        display: flex;
        flex-direction: column;
        margin-left: 10px;
        margin-right: 10px;
        transition: all 0.2s ease-in-out;
        margin-bottom: 5px;
        margin-top: 5px;
        &:hover {
          transform: scale(1.2);
        }
      }
    }
    .navLink:hover {
      opacity: 1;
    }
    .navLink::before {
      transition: 300ms;
      height: 5px;
      content: "";
      position: absolute;
      background-color: #d0c1b2;
      @media screen and (max-width: 700px) {
        display: none;
      }
    }
    .navLink-fade-up::before {
      width: 100%;
      bottom: -15px;
      opacity: 0;
    }
    .navLink-fade-up:hover::before {
      bottom: -5px;
      opacity: 1;
    }
  }
}

.navBarMain {
  display: block;

  @media screen and (max-width: 700px) {
    display: none;
  }
  ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    text-decoration: none;
    column-gap: 15px;

    margin-top: 8px;
  }
  .navLink {
    font-family: "Tajawal", sans-serif;
    font-size: 22px;
    position: relative;
    color: white;
    text-decoration: none;
    @media screen and (max-width: 950px) {
      font-size: 20px;
      margin-top: 0px;
      margin-bottom: 30px;
    }
    &:hover {
      opacity: 1;
    }
  }
  .navLink::before {
    transition: 300ms;
    height: 3px;
    content: "";
    position: absolute;
    background-color: #ddd1c5;
  }
  .navLink-fade-up::before {
    width: 100%;
    bottom: -13px;
    opacity: 0;
  }
  .navLink-fade-up:hover::before {
    bottom: -3px;
    opacity: 1;
  }
}
